.about {
    padding-bottom: 12.8rem;
    margin-bottom: 3rem;

    position: relative;

    &:after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 50%;
        z-index: 1;
        width: 4px;
        height: 80px;
        background: #EDEDED;
    }
}

.about__title {
    font-family: $oswald;
    font-size: 4.2rem;
    color: $black;
    font-weight: 400;

    text-align: center;

    margin-bottom: 5.8rem;
}

.about__bg {
    background: #EDEDED;
}

.about__inner {
    display: flex;
    align-items: center;

    padding: 2.7rem 0 4.6rem;
}

.about__list {
    display: flex;
    flex-direction: column;

}

.about__item {
    position: relative;
    font-size: 1.8rem;
    color: $black;
    font-weight: 400;
    padding: 0 2rem;

    margin-bottom: 1rem;

    &:before {
        content: "";
        position: absolute;
        top: 0.7rem;
        left: 0.7rem;
        z-index: 1;
        width: 5px;
        height: 5px;
        background: $black;
        border-radius: 100px;
    }
}

.about__center {
    display: flex;
    flex-direction: column;
    align-items: center;
}