.process {
    margin-bottom: 5rem;
}

.process__title {
    font-family: $oswald;
    font-size: 4.2rem;
    color: $black;
    font-weight: 400;

    text-transform: uppercase;
    text-align: center;

    margin-bottom: 4.8rem;
}

.process__inner {
    display: flex;

}

.process__local {
    width: 30%;

    background: #DCEFE7;
    padding: 3.1rem 2rem 0;
    margin-right: 2rem;
}

.process__local-title {
    font-family: $oswald;
    font-size: 2.8rem;
    color: $black;
    font-weight: 400;

    margin-bottom: 1.5rem;
}

.process__local-photo {

    img {
        width: 100%;
        border-radius: 1rem;
    }
}

.process__local-icon {
    position: relative;
    left: 50%;
    bottom: 3.5rem;
    z-index: 1;

    background: $white;

    width: 5.2rem;
    height: 5.2rem;
    border-radius: 50%;

    transform: translateX(-50%);

    img {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 1;

        transform: translate(-50%, -50%);
    }

    box-shadow: 0 4px 20px rgba($black, 0.25);

    cursor: pointer;
}

.process__distance {
    width: 70%;

    background: #EDEDED;
    padding: 3.1rem 2rem 0;
}

.process__distance-title {
    font-family: $oswald;
    font-size: 2.8rem;
    color: $black;
    font-weight: 400;

    margin-bottom: 2.2rem;
}

.process__distance-inner {
    display: flex;
}

.process__distance-item {
    padding: 0 1.8rem;

    &:first-child {
        padding: 0;
        padding-right: 1.8rem;
    }

    &:last-child {
        padding: 0;
        padding-left: 1.8rem;
    }
}

.process__distance-img {
    width: 10rem;

    img {

        width: 100%;
    }
    margin-bottom: 2.4rem;
}

.process__distance-text {
    font-size: 1.8rem;
    color: $black;
    font-weight: 400;

    line-height: 2.1rem;
}