.header__top-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2.3rem 0;
}

.header__center {
    display: flex;
    align-items: center;
}

.header__address {
    font-size: 1.7rem;
    color: #0E0E0E;
    font-weight: 400;

    font-style: normal;
    margin-right: 2rem;
}

.header__contact {
    display: flex;
    flex-direction: column;
}

.header__time {
    display: flex;

    img {
        margin-right: 0.5rem;
    }

    span {
        font-size: 1.6rem;
        color: #0E0E0E;
        font-weight: 400;
    }

    margin-bottom: 0.5rem;
}

.header__backcall {
    display: flex;

    img {
        margin-right: 1.1rem;
    }

    span {
        font-size: 1.6rem;
        color: #0E0E0E;
        font-weight: 400;
    }
}

.header__num {
    font-size: 3.1rem;
    color: #0E0E0E;
    font-weight: 400;

    a {
        text-decoration: none;
        color: inherit;
    }

    margin-bottom: 0.5rem;
}

.header__bottom {
    background: #0E0E0E;
}

.header__bottom-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 0;
}

.header__search {

    input {
        font-size: 1.8rem;
        color: $black;
        font-weight: 400;
        border-radius: 10rem;

        padding: 2rem 0 1rem 2.4rem;

        &::placeholder {
            color: rgba($black, 0.5);
        }

        &:focus {
            outline: none;
        }
    }
}