.study {
    background:linear-gradient(264.23deg, rgba(49, 138, 42, 0.5) 1.64%, rgba(49, 138, 42, 0) 116.91%), url('../img/study/bg.png') center no-repeat;
    background-size: cover;

    margin-bottom: 7rem;
}

.study__inner {
    display: flex;
    align-items: center;
}

.study__content {
    display: flex;
    flex-direction: column;
    width: 50%;
    padding-right: 2rem;
}

.study__text {
    position: relative;
    font-family: $oswald;
    font-size: 3.1rem;
    line-height: 4.5rem;
    color: $white;
    font-weight: 400;

    text-transform: uppercase;

    margin-bottom: 6rem;

    &:after {
        content: "";
        position: absolute;
        bottom: -3rem;
        left: 0;
        z-index: 1;
        width: 85%;
        height: 4px;
        background: $white;

    }
}

.study__span {
    font-size: 2.6rem;
    color: $white;
    font-weight: 400;

    margin-bottom: 8.5rem;
}

.study__photo {
    width: 50%;
    img {
        width: 100%;
    }
}