.video {
    padding-bottom: 11.3rem;
    margin-bottom: 3rem;

    position: relative;

    &:after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 50%;
        z-index: 1;
        width: 4px;
        height: 80px;
        background: #EDEDED;
    }
}

.video__inner {
    display: flex;

    background: url('../img/video/bg.png') left -216px center no-repeat, #EDEDED;
    padding: 3.3rem 3rem 3.3rem 5.6rem;
}

.video__title {
    width: 40%;
    font-family: $oswald;
    font-size: 4.2rem;
    line-height: 6.2rem;
    color: $black;
    font-weight: 400;

    padding-right: 15rem;
}

.video__player {
    width: 60%;

    cursor: pointer;

    img {
        width: 100%;
    }
}